.edgebutton {
    width: 20px;
    height: 20px;
    background: #eee;
    border: 1px solid #fff;
    cursor: pointer;
    border-radius: 50%;
    font-size: 12px;
    line-height: 1;
}

.edgebutton:hover {
    background: #5e35b1;
    color: #eee;
    box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.08);
}

.edgebutton-foreignobject div {
    background: transparent;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40px;
}

.reactflow-parent-wrapper {
    display: flex;
    flex-grow: 1;
    height: 100%;
}

.reactflow-parent-wrapper .reactflow-wrapper {
    flex-grow: 1;
    height: 100%;
}

.node-ai-chat {
    position: relative;
    margin-top: 84vh;
    z-index: 100000;
    background-color: gainsboro;
    /* margin-top: 82vh; */
    margin-left: 35vw;
    margin-right: 35vw;
}

.box-inline {
    display: inline-block !important;
}

.codejareditor {
    background: #222 !important;
    border-radius: 6px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    font-family: "Source Code Pro", monospace;
    font-size: 14px;
    font-weight: 400;
    height: 77vh;
    letter-spacing: normal;
    line-height: 20px;
    padding: 10px;
    tab-size: 4;
}


@media (min-width: 900px)
{
    .css-1equabv-MuiGrid-root {
        max-width: 24% !important
    }
}